.tile {
  align-items: stretch;
  display: block;
  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 1;
  min-height: -webkit-min-content;
  min-height: -moz-min-content;
  min-height: min-content;

  &.is-ancestor {
    margin-left: -.75rem;
    margin-right: -.75rem;
    margin-top: -.75rem;

    &:last-child {
      margin-bottom: -.75rem;
    }

    &:not(:last-child) {
      margin-bottom: .75rem;
    }
  }

  &.is-child {
    margin: 0 !important;
  }

  &.is-parent {
    padding: .75rem;
  }

  &.is-vertical {
    flex-direction: column;

    > .tile.is-child:not(:last-child) {
      margin-bottom: 1.5rem !important;
    }
  }
}

